import Mock from './Mock/Mock';
import Debug from '../../../config/debug';
import router from '../../../config/router';
import { Php } from '../../../vendor/lib';

const Xhr = {};
const requests = [];

if (!Debug.isDev) {
  Xhr.fetch = function (uri, options) {
    const url = router.api[uri];
    return self.fetch(url, options)
    .catch((e) => {
      const error = { error: e, created_at: Php.time() };
      self.$components.communication.failed.request.push(error);
      self.$components.communication.errorHandle(error);
    })
    .then((res) => {
      return res.text();
    })
    .then((res) => {
      try {
        return JSON.parse(res)
      } catch (e) {
        const error = { error: e, created_at: Php.time(), msg: res };
        self.$components.communication.failed.response.push(error);
        self.$components.communication.errorHandle(error);
        return res;
      }
    })
  }
} else {
  Xhr.fetch = Mock;
}

export default Xhr;


