const drag = function (el) {
  // getTouchMoveForward
  this.moveStartY = 0;
  this.moveCurrentY = 0;
  this.moveLastY = 0;
  this.forward = false;

  this.isReachLimitTop = false;

  this.scrollTopLimit = 250;
  this.scrollTopCurrent = this.scrollTopLimit;

  this.el = el.parentNode;
  this.el.className = `${this.el.className} access-token-drag`;

  el.parentNode.scrollTop = this.scrollTopLimit;
  // el.parentNode.className = `${el.parentNode.className} access-token-drag`
  el.addEventListener('touchmove', (e) => {
    e.preventDefault();
    this.forward = this.getTouchMoveForward(e);
    this.forward > 0 ? this.moveDown() : this.moveUp();
    this.draw();
  })
  el.addEventListener('touchstart', (e) => {
    this.isReachLimitTop = this.scrollTopCurrent == this.scrollTopLimit;
    this.moveCurrentY = 0;
    this.moveLastY = 0;
  })
  el.addEventListener('touchend', (e) => {
    if (this.scrollTopCurrent < this.scrollTopLimit) {
      this.recover();
    }
  })
}

drag.prototype.getTouchMoveForward = function (e) {
  this.moveCurrentY = e.changedTouches[0].pageY;
  this.moveLastY = this.moveLastY == 0 ? this.moveCurrentY : this.moveLastY;
  const forward = this.moveCurrentY - this.moveLastY;
  this.moveLastY = this.moveCurrentY;
  return forward;
}
drag.prototype.moveDown = function () {
  // is current scroll top is more than 300, analog browser logic
  if (this.scrollTopCurrent > this.scrollTopLimit) {
    this.scrollTopCurrent -= this.forward;
    this.scrollTopCurrent = this.scrollTopCurrent < this.scrollTopLimit
          ? this.scrollTopLimit : this.scrollTopCurrent;
  } else {
    // if not, drag.js delegate it
    if (!this.isReachLimitTop) {
      return false;
    }
    const mapMoveYBefore = (this.scrollTopLimit - this.scrollTopCurrent) / this.scrollTopLimit;
    const mapMoveXBefore = Math.asin(mapMoveYBefore) * 150;
    const mapMoveXAfter = mapMoveXBefore + 6;
    const mapMoveYAfter = Math.sin(mapMoveXAfter / 150) * this.scrollTopLimit;
    this.scrollTopCurrent = this.scrollTopLimit - mapMoveYAfter;
  }
}
drag.prototype.moveUp = function () {
  this.scrollTopCurrent -= this.forward;
  if (this.scrollTopCurrent > 900) {
    this.scrollTopCurrent = 900;
  }
}
drag.prototype.draw = function () {
  this.el.scrollTop = this.scrollTopCurrent;
}
drag.prototype.recover = function () {
  // if (this.scrollTopCurrent == this.scrollTopLimit) {
    // return false;
  // }
  let rafId;
  const raf = () => {
    // if (this.isPaused) {
      // rafId = requestAnimationFrame(raf);
      // return false;
    // }
    const mapMoveYBefore = (this.scrollTopLimit - this.scrollTopCurrent) / this.scrollTopLimit;
    const mapMoveXBefore = Math.asin(mapMoveYBefore) * 150;
    const mapMoveXAfter = mapMoveXBefore - 6;
    const mapMoveYAfter = Math.sin(mapMoveXAfter / 150) * this.scrollTopLimit;
    this.scrollTopCurrent = this.scrollTopLimit - mapMoveYAfter;

    // if (typeof watch == 'function' && !this.isUnwatched) {
      // watch(this);
    // }

    this.scrollTopCurrent = this.scrollTopCurrent >= this.scrollTopLimit
      ? this.scrollTopLimit : this.scrollTopCurrent;

    this.draw()
    rafId = requestAnimationFrame(raf);
    if (this.scrollTopCurrent >= this.scrollTopLimit) {
      // this.isUnwatched = false;
      cancelAnimationFrame(rafId);
    }
  }
  rafId = requestAnimationFrame(raf);
}

export default drag;


