<template>
  <div id="app">
    <div class="vendor-components">
      <ComponentsMobile />
      <ComponentsRedirect />
      <ComponentsDrag />
    </div>
    <vendor-components class="core"></vendor-components>
    <router-view></router-view>
  </div>
</template>
<script>
  import { ComponentsMobile } from './components/ComponentsMobile';
  import ComponentsRedirect from './components/ComponentsRedirect';
  import ComponentsDrag from './components/ComponentsDrag';

  export default {
    components: { ComponentsMobile, ComponentsRedirect, ComponentsDrag }
  }
</script>
<style>
  .wrap {
    position: absolute;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
  }
  .container {
    flex-grow: 1;
    flex-shrink: 1;
    flex-basis: 0%;
    overflow-y: scroll;
    overflow-x: hidden;
  }
</style>


