<template>
  <div>
    <slot name="header"></slot>
    <div>
      <slot
        name="item"
        v-for="(item, index) in payload"
        :index="index"
        :item="item">
      </slot>
    </div>
  </div>  
</template>
<script>
  import { Func } from '../../vendor/lib';
  import Drag from './Drag';

  export default {
    data () {
      return {
        items: {}, item: '',
        move: 0,
        moveStartY: 0, moveEndY: 0,
        moveLastY: 0, moveCurrentY: 0,
        moveFrameCount: 0, moveFrameCurrent: 0,
      }
    },
    props: ['payload'],
    mounted () {
      this.$register('oldDrag')
      .then((payload) => {
        const drag = new Drag(this.$el, {
          move: payload.move,
          end: payload.end,
        });
      })
    }
  }
</script>
<style scoped>
  .loadmore {
    z-index: -1;
  }
</style>


