export default {
  // left part is the real routes, right part is the path of the page
  routes: {
    '/order/payload/exercises/record':
      () => 
        import(/* webpackChunkName: "ekrace" */ '../views/ekrace/exercises/record'),
    '/order/payload/exercises/history':
      () => 
        import(/* webpackChunkName: "ekrace" */ '../views/ekrace/exercises/history'),
    '/order/payload/custom/toast':
      () => 
        import(/* webpackChunkName: "ekrace" */ '../views/ekrace/custom/toast'),
    '/order/payload/custom/service':
      () => 
        import(/* webpackChunkName: "ekrace" */ '../views/ekrace/custom/service'),
    '/order/payload/custom/service/([0-9]+)':
      () => 
        import(/* webpackChunkName: "ekrace" */ '../views/ekrace/custom/answer'),
    '/': require('../views/test'),
  },
  // left part is use in h5, right part is mapping from fake routes to real routes
  api: {
    '/exercises/getRecord': '/order/payload/exercises/getRecord',
    '/exercises/history': '/order/payload/exercises/history',
    '/custom/service/([0-9]+)': '/order/payload/custom/service/$1',
    '/custom/service': '/order/payload/custom/service',
    '/exercises/record': '/order/payload/exercises/record',
    '/custom/toast': '/order/payload/custom/toast',
  }
}


