<template><div></div></template>
<script>
  import router from '../config/router';

  export default {
    mounted () {
      this.$register('redirect').then((payload) => {
        let { path } = payload;
        const patternRouteMatch = (path) => {
          for (let [key, value] of Object.entries(router.api)) {
            key = key.replace(/\//g, '\\/');
            const pattern = new RegExp(key);
            const matches = path.match(pattern);
            if (matches) {
              const replaces = value.match(/\$[0-9]/g);
              if (!replaces) {
                return value;
              }
              for (let i = 0; i < replaces.length; i++) {
                value = value.replace(
                  replaces[i],
                  matches[replaces[i].replace(/\$/, '')]
                );
                return value;
              }
            }
          }
          return path;
        }
        path = patternRouteMatch(path);
        this.$router.push({ path });
      })
    }
  }
</script>


