import { Core } from '../../../../vendor/lib';

const requireMock = function (mockName) {
  const mock = require(`../../../../mock/${mockName}`);
  return mock.default;
}

const Mock = function (url, options) {
  const req = Core.bodyParse(url);
  const res = req.controller;
  const mock = requireMock(res)[req.action];

  return Promise.resolve(JSON.parse(mock));
}

export default Mock;


