<template></template>
<script>
  import Drag from './lib/new';

  export default {
    mounted () {
      this.$register('drag')
      .then((payload) => {
        const { el } = payload;
        new Drag(el);
      })
    }
  }
</script>
<style>
  .access-token-drag::-webkit-scrollbar {
    display: none;
  }
</style>


